import { Component, ViewEncapsulation } from '@angular/core';
import { GetLaugisticService } from '../services/GetLaugisticService';
import { GetOrderService } from '../services/GetOrderService';
import { PendingOrderService } from '../services/PendingOrderService';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { GetLaugisticModel } from '../models/GetLaugisticModel';
import { GetAllProductModel } from '../models/GetAllProductModel';
import { UpdateOrderStatusService } from '../services/UpdateOrderStatusService';
import { GetOrderDetailModel } from '../models/GetOrderDetailModel';


@Component({
  selector: 'UpdateOrderStatus',
  styles: [require('./UpdateOrderStatus.style.scss')],
  template: require('./UpdateOrderStatus.template.html'),
  providers: [GetLaugisticService, PendingOrderService, GetOrderService, UpdateOrderStatusService],
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'login-page app'
  }
})
export class UpdateOrderStatusComponent {
  public isAdmin: boolean;
  public UserType:any;
  public isChemist: boolean;
  public GetOrderDetailModelArray: GetOrderDetailModel[] = [];
  public GetAllProductModelArray: GetOrderDetailModel[] = [];
  public GetLaugisticModelArray: GetLaugisticModel[] = [];
  public GetLaugisticModeldata: GetLaugisticModel[] = [];
  public username: string;
  public fullName: any;
  public Chemistusername: any;
  public Chemistpassword: any;
  public password: string;
  public router: Router;
  public AdminUserType: any;
  public ChemistUserType: any;
  public Lougistic_Id: any;
  public Order_Id: any;
  public Status_Name: any;
  public OrderStatus_Id: any;
  public PendingOrderServiceArray: PendingOrderService[] = [];
  public PENDING:any;
  public ONTHEWAY :any;
  public DELIVERED :any;
  public CONFIRMED :any;
  public SHIPPED :any;
  public NOTSHIPPED :any;
  public CANCELED :any;
  public COMPLETED :any;
  public RETURNED :any;
  public TOBEDELIVERED :any;


  constructor(public _UpdateOrderStatusService: UpdateOrderStatusService, public _getLaugisticService: GetLaugisticService, public _getPendingOrderService: PendingOrderService, public _getOrderService: GetOrderService, router: Router, ) {
    this.UserType=localStorage.getItem("UserType");
    if(this.UserType==null)
    {
      this.router.navigate(["/app/login"]);
    
    }
    else
    {
    this.router = router;
  }
}
  getStatusCategory(StatusName) {
    this.Status_Name = StatusName;
    console.log(StatusName);
    this.PENDING = "pending";
    this.ONTHEWAY = "On The way";
    this.DELIVERED = "Delivered";
    this.CONFIRMED = "Confirmed";
    this.SHIPPED = "Shipped";
    this.NOTSHIPPED = "Not Shipped";
    this.CANCELED = "Canceled";
    this.COMPLETED = "Completed";
    this.RETURNED = "Returned";
    this.TOBEDELIVERED = "To Be Delivered";
  
 
  this.username = undefined;
  this.password = undefined;

  this._getOrderService.GetOrderDetailService(this.Status_Name).subscribe(
    response => {
      console.log(response.data);
      this.GetAllProductModelArray = response.data;

      jQuery("#snackbar").html(response.message);
      this.myFunction();
      for (var i = 0; i < response.data.length; i++) {
        if (this.GetAllProductModelArray[i].OrderStatus == 100) {

          this.GetAllProductModelArray[i].OrderStatus =this.PENDING;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 200) {

          this.GetAllProductModelArray[i].OrderStatus = this.ONTHEWAY;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 300) {
          this.GetAllProductModelArray[i].OrderStatus = this.DELIVERED;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 400) {

          this.GetAllProductModelArray[i].OrderStatus = this.CONFIRMED;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 500) {

          this.GetAllProductModelArray[i].OrderStatus = this.SHIPPED;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 600) {

          this.GetAllProductModelArray[i].OrderStatus = this.NOTSHIPPED;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 700) {

          this.GetAllProductModelArray[i].OrderStatus = this.CANCELED;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 800) {
          this.GetAllProductModelArray[i].OrderStatus = this.COMPLETED;
        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 900) {

          this.GetAllProductModelArray[i].OrderStatus = this.RETURNED;

        }
        else if (this.GetAllProductModelArray[i].OrderStatus == 10) {

          this.GetAllProductModelArray[i].OrderStatus = this.TOBEDELIVERED;

        }
        else {

          alert("Not Available");
        }
      }

    });


  console.log(this.GetLaugisticModelArray);
  }
  GetOrderStatusId(OrderStatusId) {
    // this.OrderStatus_Id = OrderStatusId;
    //console.log(this.OrderStatus_Id);
  }
  GetOrderId(OrderId) {
    //  this.Order_Id = OrderId;
    // console.log(this.Order_Id);
  }

  GetOrderAndStatusfunction(orderId, UserStatus) {
    this.OrderStatus_Id = this.Status_Name;
    console.log(this.OrderStatus_Id);
    this.GetOrderId = orderId;
    console.log(this.GetOrderId);
    jQuery("#snackbar").html("Selected Row Successfully");
    this.myFunction();
  }

  pendingOrderFun() {

  }

  UpdateOrderStatus() {
    this._UpdateOrderStatusService.UpdateOrderDetailService(this.OrderStatus_Id, this.GetOrderId).subscribe(
      response => {
        console.log(response);
        var OrderIdchanged = response.data.Id;

        var obj = this.GetAllProductModelArray.find(x=>x.Id == OrderIdchanged);
        obj.OrderStatus = response.data.OrderStatus;
        console.log(obj);
      
          if (obj.OrderStatus == 100) {

            obj.OrderStatus =this.PENDING;

          }
          else if (obj.OrderStatus == 200) {

            obj.OrderStatus  = this.ONTHEWAY;

          }
          else if (obj.OrderStatus  == 300) {
            obj.OrderStatus  = this.DELIVERED;

          }
          else if (obj.OrderStatus == 400) {

            obj.OrderStatus = this.CONFIRMED;

          }
          else if (obj.OrderStatus  == 500) {

            obj.OrderStatus = this.SHIPPED;

          }
          else if (obj.OrderStatus == 600) {

            obj.OrderStatus  = this.NOTSHIPPED;

          }
          else if (obj.OrderStatus  == 700) {

            obj.OrderStatus  = this.CANCELED;

          }
          else if (obj.OrderStatus  == 800) {
            obj.OrderStatus  = this.COMPLETED;
          }
          else if (obj.OrderStatus  == 900) {

            obj.OrderStatus  = this.RETURNED;

          }
          else if (obj.OrderStatus  == 10) {

            obj.OrderStatus  = this.TOBEDELIVERED;

          }
          else {

            alert("Not Available");
          }
         

     
     
      });
  }
  GetLaugisticId(LougisticId) {
    this.Lougistic_Id = LougisticId;
  }

  myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }



}



